export default {
	data() {
		return {
			list:[],
			name:"",
			phone:"",
			title:'',
			title_phone:'',
			area:"",
			id:"",
			content:"",
			textMessage:"",
		}
	},
	onLoad:function(option){ 
		var _self = this
		_self.id = option.id
		_self.getInfo()
	},
	methods: {
		previewImage: function(e) {
			uni.previewImage({
				current: e,
				urls: [e]
			})
		},
		getInfo:function(){
			var _self = this
			uni.request({
				url:_self.$api+"dockingManager/pictureQuery",
				data:{id:_self.id},
				method:"GET",
				success:function(res){
					console.log(res)
					var data = res.data[0]
					_self.list = res.data[0]
					_self.phone = res.data[0].phone
					if(_self.list.content == undefined || _self.list.content==null){
						_self.list.content = ""	
					}else{
						_self.content = _self.list.content
					}
				}
				
			})
		},
		apply:function(){
			var _self = this
			 uni.makePhoneCall({
				phoneNumber: _self.phone //仅为示例
			 });
		},
		addData:function(){
			var _self = this
			var myreg = /^((0\d{2,3}-\d{7,8})|(1[3456789]\d{9}))$/;
			if(_self.title == ""){
					uni.showToast({
					title:"请输入您的姓名",
					icon:"none"
				})
				return false
			}
			if(_self.title_phone == ""){
					uni.showToast({
					title:"请输入您的联系方式",
					icon:"none"
				})
				return false
			}
			if(!myreg.test(_self.title_phone)){
					uni.showToast({
					title:"联系电话格式不正确",
					icon:"none"
				})
				return false
			}
			uni.request({
				url:_self.$api+"dockingManager/stayAdd",
				data:{
					id:_self.id,
					optionId:uni.getStorageSync("openId"),
					name:_self.title,
					sketch:'',
					phone:_self.title_phone
				},
				success:function(res){
					console.log(res)
					if(res.data ==1){
						uni.showToast({
							title:"请求成功",
							success:function(){
								setTimeout(function(){
									uni.switchTab({
										url:"../../../pages/index/index"
									})
								},1000)
							}
						})
						return false
					}else if(res.data==99){
						uni.showModal({
							title: '提示',
							content: '此操作需用户授权，是否进行授权',
							success: function (res) {
								if (res.confirm) { 
									//跳转到授权页面  
									uni.navigateTo({
										url:"../../login/login"  
									})
									console.log('用户点击确定');
								} else if (res.cancel) {
									console.log('用户点击取消');
								}
							}
						});
					}
					else{
							uni.showToast({
							title:"申请失败",
							icon:"none"
						})
						return false
					}
				},
			})
		},
		
	} 
	
}
